body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Для удобства рабочие классы */

.dFlex {
  display: flex;
}

.flexRow {
  display: flex;
  flex-direction: row;
}

.flexColumn {
  display: flex;
  flex-direction: column;
}

.flexItemsCenter {
  align-items: center;
}

.flexContentCenter {
  justify-content: center;
}

.dBlock {
  display: block;
}

.container {
  max-width: 1280px;
}

.centerBlock {
  margin: 0 auto;
}

.centerText {
  text-align: center;
}

/* Доп стили для верхней булки */
.constructor-element_pos_top {
  width: 504px !important;
  align-self: end;
  margin-left: 36px !important;
}

/* Доп стили для нижней булки  */
.constructor-element_pos_bottom {
  width: 508px !important;
  align-self: end;
  margin-left: 40px !important;
}

/* Доп стили для компонента Tabs */
.tab.pt-4.pr-10.pb-4.pl-10.noselect {
  justify-content: center;
  display: flex;
  width: 100%;
}

/* Доп стили для компонента ConstructorElement */
.constructor-element {
  margin-right: 16px;
}

.constructor-element_pos_top {
   max-height: 80px;
}

.constructor-element_pos_bottom {  
  max-height: 80px;
}


/* полоса прокрутки (скроллбар) */
::-webkit-scrollbar {
  width: 8px;
  /* ширина для вертикального скролла */
  height: 8px;
  /* высота для горизонтального скролла */
  background-color: #143861;
}

/* ползунок скроллбара */
::-webkit-scrollbar-thumb {
  background-color: #8585ad;
  /* border-radius: 9em;
  box-shadow: inset 1px 1px 10px #f3faf7; */
}

::-webkit-scrollbar-thumb:hover {
  background-color: #253861;
}

a.active p {
  color: white;
}