.profileWrapper {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  gap: 100px;
  width: 100%;
}

.profileLeftSide {
  width: 40%;
}

.profileRightSide {
  width: 60%;
  display: flex;
  flex-wrap: wrap;
  height: calc(100vh - 220px);
  padding-bottom: 15px;  
}

.scrollSection {  
  overflow-y: scroll;
  height: calc(100vh - 220px);
}

.profileNav {
  display: flex;
  flex-direction: column; 
  gap: 20px;  
  text-align: left;  
}

.profileNav a {
  text-decoration: none;
}

.activeLink {
  color:white;
}

.profileForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}

.buttons {
  display: flex;
  justify-content: flex-end;
  gap: 20px;
}

.buttonLink {
  color: #4C4CFF;
  border: none;
  background: none;
  cursor: pointer;
}
